/*********************/
/** Personal styles */
/*********************/

$blue: #29b6f6;
$green: #9ccc65;
$purple: #ba68c8;
$orange: #f57c00;
$red: #ef5350;
$cyan: #4dd0e1;
$dark: #1c1c1c;
$background-light: #ffa726;
$background-dark: #ff9800;
$text-muted: #555;


.arch {
  text-align: center;
  letter-spacing: 2px;
  margin: 20px 0 20px 0;

  .orange, a .orange, a:hover {
    color: $orange;
  }

  .name {
    text-shadow: 0 0 1px black;
    color: $orange;
  }

  .contact-links {
    display: flex;
    justify-content: center;
  }
}

.arch-container {
  margin: 20px 0 30px 0;
}

.text-muted {
  color: $text-muted;
}

.about-page-img {
  max-width: 100%;
  padding: 20px 0 20px 0;
}

.separate {
  margin: auto;
  display: flex;
  justify-content: space-between;
}

.box-row {
  display: flex; 
  justify-content: center;
}

.box-desc {
    background: $dark;
    color: $orange;
    border-radius: 3px;
    padding: 30px; 
    margin: 20px;
    box-shadow: 0 1.5rem 4rem rgba(black, 0.4);
    min-width: 480px;
    .button-cont {
      margin-top: 20px;
    }
}

.text-uppercase {
  text-transform: uppercase;
}

.font-weight-light {
  font-weight: 200;
}

.font-weight-bold {
  font-weight: 600;
}


.box-desc a {
	color: gray;
	padding: 1rem 2rem 1rem 2rem;
	line-height: 40px;
	border: 0.1rem solid gray;
	border-radius: 3px;
	-webkit-transition: all 0.5s ease-in-out;
	-moz-transition: all 0.5s ease-in-out;
	-o-transition: all 0.5s ease-in-out;
	transition: all 0.5s ease-in-out;

}

.box-desc a:hover {
	border-color: $orange;
	color: $orange;
}

